


























































































































































































































































import Vue from "vue";
import { mask } from 'vue-the-mask';

import UnitPlanOa from "@/models/UnitPlanOa";
import UnitPlanWeek from '@/models/UnitPlanWeek';
import UnitPlanWeekIe from '@/models/UnitPlanWeekIe';
import UnitPlan from '@/models/UnitPlan';

export default Vue.extend({
    name: "RomaCoverage",
    created () {
      this.selectedMenu = this.menu;

      // Cobertura existente
      const ies = this.oasAdded.map(detail => detail.oa.ies);

      this.unit.coverage.forEach(week => {
          let data = {
              id: week.id,
              unitplan_id: this.unit.id,
              ie_id: null,
              week: {
                  start: week.startDate,
                  end: week.endDate,
              },
          };

          week.ies.forEach(ie => {
              data.ie_id = ie.id;

              
          })
      });
    },
    props: {
        unit: {
            type: Object,
            required: true,
        },
        titleWidth: {
            type: String,
            default: '480'
        },
        height: {
            type: String,
            default: '514'
        },
        applyRowColor: {
            type: Boolean,
            default: true
        },
    },
    data() {
        return {
            x: 0,
            y: 0,
            expanded: [],
            showingMenu: false,
            selectedMenu: null,
            menuStartDate: false,
            coverageCount: [],
            selectedIe: null, // Ie a modificar
            firstMonth: new Date().toISOString().substr(0, 7),
            secondMonth: new Date(),
            request: null, // modificación del ie
            requestWeeks: [],
            menu: [
                {
                    label: 'Asignar IE',
                    color: 'green',
                    onClick: () => {
                        this.request.color = 'green';
                        this.assignIe()
                    }
                },
                {
                    label: 'Asignar Evaluación',
                    color: 'blue',
                    onClick: () => {
                        this.request.color = 'blue';
                        this.assignIe()
                    }
                },
            ],
            altMenu: [
                {
                    label: 'Eliminar asignación',
                    onClick: () => {
                       let index = this.selectedIe.request.findIndex(r => { return r.ie_id === this.request.ie_id && r.week === this.request.week});
                        this.$delete(this.selectedIe.request, index);
                        this.coverageCount.pop(); // Solo para botón borrar toda la cobertura
                    }
                },
            ],
            headers: [
                { text: '1', value: '1' },
                { text: '2', value: '2' },
                { text: '3', value: '3' },
                { text: '4', value: '4' },
                { text: '5', value: '5' },
            ],
        };
    },
    watch: {
        showingMenu (value) {
            if (!value) {
                this.selectedMenu = this.menu; // Devuelvo elementos del menu si se cerro
            }
        }
    },
    computed: {
        oasAdded () {
            return UnitPlanOa.query()
                .where('unitplan_id', this.unit.id)
                .where('disabled', false)
                .with('oa', query => {
                    query.with('ies')
                })
                .get();
        },
        canDeleteAllCoverage () {
            return this.coverageCount.length > 0;
        },
        getFirstMonth () {
            const firstMonth = new Date(this.firstMonth);
            return this.getWeeksInMonth(firstMonth.getFullYear(), firstMonth.getMonth()+1);
        },
        getSecondMonth () {
            const firstMonth = new Date(this.firstMonth);
            let year = firstMonth.getFullYear();

            this.secondMonth = new Date(year, firstMonth.getMonth()+2);
            return this.getWeeksInMonth(this.secondMonth.getFullYear(), this.secondMonth.getMonth());
        },
        getBothMonths () {
          return this.getFirstMonth.concat(this.getSecondMonth);
        },
        getNameFirstMonth () {
            const firstMonth = new Date(this.firstMonth);
            const date = new Date(firstMonth.getFullYear(), firstMonth.getMonth()+1, firstMonth.getDay());
            return date.toLocaleString('default', { month: 'long' });
        },
        getNameSecondMonth () {
            return this.secondMonth.toLocaleString('default', { month: 'long'});
        }
    },
    methods: {
        selectIe (ie, week, numWeek, e) {
            e.preventDefault();

            // Request del ie
            this.selectedIe = ie;
            this.request = {
                ie_id: ie.id,
                week: week,
            };

            // this.requestWeeks

            // Verificar menu a usar
            if (this.selectedIe.request && Array.isArray(this.selectedIe.request)) {
                if (this.selectedIe.request.find(r => { return r.ie_id === this.request.ie_id && r.week === this.request.week})) {
                    this.selectedMenu = this.altMenu;
                } else {
                    this.selectedMenu = this.menu;
                }
            }

            this.showingMenu = false;
            this.x = e.clientX;
            this.y = e.clientY;
            this.$nextTick(() => {
                this.showingMenu = true;
            })
        },
        assignIe () {
            if (!Array.isArray(this.selectedIe.request)) { // Si no es arreglo, convierto
                this.selectedIe.request = [];
            }

            if (!this.selectedIe.request.find(r => { return r.ie_id === this.request.ie_id && r.week === this.request.week})) { // si no existe en el array
                this.selectedIe.request.push(this.request);
                this.coverageCount.push(this.request.ie_id); // Solo para botón borrar toda la cobertura
            }
        },
        deleteAllCoverage () {
          this.oasAdded.forEach(detail => detail.oa.ies.forEach(ie => this.$set(ie, 'request', [])));
          this.coverageCount = [];
        },
        saveCoverage () {
            const msj = {
                show: true,
                text: '¡Cobertura de la unidad Guardada!',
                color: 'success',
            };

            let request = [];
            const ies = this.oasAdded.map(detail => detail.oa.ies);

            // Filtro solo los que tengan request
            for (let i in ies) {
                if (ies[i].filter(ie => ie.request).length > 0) {
                    request.push(ies[i].filter(ie => ie.request && ie.request.length > 0))
                }
            }

            // Actualiza horas de los oas
            const oas = this.oasAdded.map(detail => {
                return {id: detail.id, hours: detail.hours}
            });
            UnitPlanOa.update({data: oas});

            if (request[0].length > 0) {
                request[0].forEach(
                    ie => {
                        for (let i in ie.request) {
                            const unitplanweek = UnitPlanWeek.query().
                                                    where('unitplan_id', this.unit.id).
                                                    where('startDate', ie.request[i].week.start).
                                                    where('endDate', ie.request[i].week.end).
                                                    first();

                            const type = ie.request[i].color === 'blue' ? 2 : 1;

                            if (!unitplanweek) {
                                const data = {'unitplan_id': this.unit.id, 'startDate': ie.request[i].week.start, 'endDate': ie.request[i].week.end};
                                UnitPlanWeek.insert({data: data}).then(data => {
                                    UnitPlanWeekIe.insert({data: {unitplanweek_id: data.unitplan_weeks[0].id, ie_id: ie.id, type: type}});
                                })
                            } else {
                                UnitPlanWeekIe.insert({data: {unitplanweek_id: unitplanweek.id, ie_id: ie.id, type: type}});
                            }
                        }
                    }
                );
            }

            this.$store.state.snackbar = msj;
        },
        getWeeksInMonth(year: number, month: number) {
            const weeks = [];
            const firstDay: Date = new Date(year, month, 1);
            const lastDay: Date = new Date(year, month + 1, 0);
            const daysInMonth: number = lastDay.getDate();
            let dayOfWeek: number = firstDay.getDay();
            let start: number;
            let end: number;

            for (let i = 1; i < daysInMonth + 1; i++) {

                if (dayOfWeek === 0 || i === 1) {
                    start = i;
                }

                if (dayOfWeek === 4 || i === daysInMonth) { // 4 = Viernes, final de la semana

                    end = i;

                    if (start !== end) {

                        weeks.push({
                            // Concateno año y mes al dia de la semana
                            start: firstDay.toISOString().substr(0, 7) + '-' + start,
                            end: firstDay.toISOString().substr(0, 7) + '-' + end
                        });
                    }
                }

                dayOfWeek = new Date(year, month, i).getDay();
            }

            return weeks;
        },
        markColor (ie, week) {
            return ie.request.find(r => { return r.ie_id === ie.id && r.week === week}).color;
        },
        rowStyle (itemIndex: number, disabled: boolean) {
            const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
            let color = colors[itemIndex];
            color = `var(${colors[itemIndex % colors.length]})`;

            if (disabled || !this.applyRowColor) color = 'grey';

            return `border-left: 8px solid ${color};`;
        }
    },
    directives: {
        mask
    }
});
