

































































































































import Vue from "vue";
import { createSimpleTransition } from 'vuetify/src/util/helpers';
import { Ripple } from 'vuetify/lib/directives';

import RomaConfirmDialog from '@/components/partial/RomaConfirmDialog.vue';

const romaCrudTransition = createSimpleTransition('unit-list-transition');

Vue.component('transition-crud', romaCrudTransition);

export default Vue.extend({
    name: "RomaCrud",
    created () {
      this.items = this.model.all(); // TODO: Evitar mutación
    },
    props: {
       title: String,
       model: Function,
       canEdit: {
            type: Boolean,
            default: true,
        },
        canAdd: {
            type: Boolean,
            default: true,
        },
        canDelete: {
            type: Boolean,
            default: true,
        },
        headerFormat: {
            type: Object,
            default () {
                return {}
            }
        },
    },
    data() {
        return {
            items: [],
            selectedItem: null,
            dialog: false,
            selected: [],
        };
    },
    watch: {
      model (value) {
          this.items = this.model.all(); // Observamos cambios en el modelo
      }
    },
    computed: {
        headers () {
            let text = '';
            let align = 'left';
            let sortable = true;
            let visible = true;
            let headerClass = null;
            let headers = [];
            const reservedWors = ['created_at', 'disabled', 'id', 'update_at'];
            const fields = this.model.fields();

            for (const index in fields) {
                text = (this.headerFormat[index] && this.headerFormat[index].text) ? this.headerFormat[index].text : index.replace('_', ' ');
                if (this.headerFormat[index]) {
                    if (this.headerFormat[index].visible) visible = this.headerFormat[index].visible;
                    if (this.headerFormat[index].align) align = this.headerFormat[index].align;
                    if (this.headerFormat[index].sortable) sortable = this.headerFormat[index].sortable;
                    if (this.headerFormat[index].headerClass) headerClass = this.headerFormat[index].headerClass;
                }

                if (visible) {
                    headers.push({
                        text: text,
                        value: index,
                        align: align,
                        sortable: true,
                        editable: reservedWors.indexOf(index) >= 0 ? false : true
                    });
                }
            }

            // Acciones
            headers.push({ text: 'Acciones', value: 'action', align: 'right', sortable: false });

            return headers;
        },
    },
    methods: {
        defaultItem () {
            let defaultItem = {};
            const fields = this.model.fields();

            for (const index in fields) {
                defaultItem[index] = fields[index].value;
            }

            return defaultItem;
        },
      addItem () {
          // TODO: Validar guardar boton en un futuro
          this.items.push(this.defaultItem());
      },
      save (item) {
        const msj = {
            show: true,
            text: '¡Maestro Guardado Con Exito!',
            color: 'success',
        };
        this.model.insertOrUpdate({data: item});

        this.$emit('master-saved', item);
        this.$store.state.snackbar = msj;
      },
      beforeDelete(item: object) { // Levanta cuadro de confirmación
          this.dialog = true;
          this.selectedItem = item;
      },
      deleteItem () {
          // FIXME: No esta borrando el item de la grilla
          const index = this.items.indexOf(this.selectedItem);
          this.model.delete(this.selectedItem.id);
          this.dialog = false;
          this.items.slice(index, 1);
      }
    },
    components: {
        RomaConfirmDialog
    },
    directives: {
        Ripple
    }
});
