

























































































































































import Vue from "vue";

import RomaCrud from './RomaCrud.vue'

// Models
import Attitude from '@/models/masters/Attitude';
import PreLearning from '@/models/masters/PreLearning';
import Skill from '@/models/masters/Skill';
import UnitPlan from "@/models/UnitPlan";
import UnitPlanPreLearning from "@/models/UnitPlanPreLearning";
import UnitPlanSkill from "@/models/UnitPlanSkill";
import UnitPlanAttitude from "@/models/UnitPlanAttitude";

export default Vue.extend({
    name: "RomaPurpose",
    created () {
        this.request.attitudes = this.unit.attitudes;
        this.request.preLearnings = this.unit.preLearnings;
        this.request.skills = this.unit.skills;
        this.request.description = this.unit.description;
    },
    props: {
        unit: {
            type: Object,
            required: true,
        }
    },
    data() {
        return {
            dialog: false,
            valid: false,
            masterSelected: 'preLearnings',
            request: {
                description: '',
                preLearnings: [],
                skills: [],
                attitudes: [],
            },
        };
    },
    computed: {
        crud () {
            const tree = {
                attitudes: {
                    model: Attitude,
                    title: 'Maestro de Actitudes',
                },
                preLearnings: {
                    model: PreLearning,
                    title: 'Maestro de Aprendizaje',
                },
                skills: {
                    model: Skill,
                    title: 'Maestro de Habilidades',
                },
            };
            return tree[this.masterSelected];
        },
        attitudes () {
            return Attitude.all();
        },
        preLearnings () {
            return PreLearning.all();
        },
        skills () {
          return Skill.all();
        }
    },
    methods: {
        savePurpose () {
            const msj = {
                show: true,
                text: '¡Propósito de la unidad Guardado!',
                color: 'success',
            };

            let record = {unitplan_id: this.unit.id};
            // Descripción propósito
            this.unit.description = this.request.description;
            UnitPlan.update({data: this.unit});
            let item = {unitplan_id: this.unit.id};
            // Aprendizaje previo
            if (this.request.preLearnings.length > 0) {
                UnitPlanPreLearning.delete(i => i.unitplan_id === this.unit.id);
                this.request.preLearnings.forEach(i => {
                    item.preLearning_id = i.id;
                    UnitPlanPreLearning.insert({data: item});
                });
            }
            // Habilidades
            if (this.request.skills.length > 0) {
                UnitPlanSkill.delete(i => i.unitplan_id === this.unit.id);
                this.request.skills.forEach(i => {
                    item.skill_id = i.id;
                    UnitPlanSkill.insert({data: item});
                });
            }
            // Actitudes
            if (this.request.attitudes.length > 0) {
                UnitPlanAttitude.delete(i => i.unitplan_id === this.unit.id);
                this.request.attitudes.forEach(i => {
                    item.attitude_id = i.id;
                    UnitPlanAttitude.insert({data: item});
                });
            }

            this.$store.state.snackbar = msj;
        },
        selectMaster (index: string) {
            this.masterSelected = index;
            this.dialog = true;
        }
    },
    components: {
        RomaCrud
    }
});
