






































































































































































import Vue from 'vue';
import RomaOa from "@/components/RomaOa.vue";
import UnitPlanOa from '@/models/UnitPlanOa';
import UnitPlanActivity from '@/models/UnitPlanActivity';
import UnitPlanActivityOa from '@/models/UnitPlanActivityOa';
import UnitPlanActivityFile from '@/models/UnitPlanActivityFile';
import UnitPlanActivityDua from '@/models/UnitPlanActivityDua';
import DuaCategory from "@/models/DuaCategory";
import Oa from "@/models/Oa";

function requestDefaultValues () {
    return {
        selectedOAs: [],
        activityLer: '',
        files: [],
        selectedDuaCategory: null,
        selectedDuas: [],
        activityEva: {
            type: null,
            instrument: null,
            text: '',
        }
    }
}

export default Vue.extend({
    name: 'RomaActivityModalForm',
    props: {
        value: Boolean,
        activity: Object,
        maxWidth: {
            type: String,
            default: '1024',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
        unit: {
            type: Object,
            required: true,
        },
        selected: {
            type: Array
        }
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            oaView: RomaOa,
            evaluationTypes: [],
            instrumentTypes: [],
            request: requestDefaultValues(),
        };
    },
    computed: {
        unitOAs () {
            const details = UnitPlanOa.query()
                .where('unitplan_id', this.unit.id)
                .with('oa')
                .get();

            return details.map(detail => detail.oa);
        },
        data () {
            let oas = Oa.query().with('ies').get();

            oas = oas.map(oa => {
                return new UnitPlanOa({
                    id: oa.id,
                    unitplan_id: this.unit.id,
                    oa_id: oa.id,
                    oa: oa,
                    selectedRows: false,
                })
            });

            return {details: oas};
        },
        duas () {
            let menu = [];
            DuaCategory.query().with('duas').get().forEach(category => {
                menu.push({header: category.name});
                category.duas.forEach(dua => {
                    menu.push({id: dua.id, name: dua.name, cod: dua.cod})
                })
            });
            return menu;
        },
    },
    watch: {
        activity (value) {
            if (value) {
                this.request.activityLer = value.name;
                this.request.selectedOAs = value.oas;
                if (value.duas.length > 0) {
                    this.request.selectedDuaCategory = this.duas.find(cat => cat.id === value.duas[0].duacategory_id);
                }
                this.request.selectedDuas = value.duas;
                this.request.files = value.files;
            } else {
                this.request = requestDefaultValues();
            }
        },
        selected (value) {
            if (value.length > 0) {
                this.request.selectedOAs = [];
                value.forEach(v => {
                    this.unitOAs.forEach(o => {
                        if (v.id === o.id) this.request.selectedOAs.push(o);
                    })
                })
            }
        }
    },
    methods: {
        show(value: Boolean) {
            this.$emit('input', value);
        },
        selectOAs (event) {
            this.request.selectedOAs = event;
        },
        save () {
            let data = {unitplan_id: this.unit.id, name: this.request.activityLer};
            if (this.activity) data.id = this.activity.id; // Si va a editar

            UnitPlanActivity.insertOrUpdate({data: data}).then(response => { // Si ya se inserto la actividad
                // OAs
                if (this.request.selectedOAs.length > 0) {
                    // Si actualiza borro oas pertenecientes a la actividad
                    if (this.activity) UnitPlanActivityOa.delete(activityOa => activityOa.activity_id === this.activity.id);
                    this.request.selectedOAs.forEach(oa => {
                        UnitPlanActivityOa.insert({
                            data: {
                                activity_id: response.unitplan_activities[0].id,
                                oa_id: oa.id,
                            }
                        })

                    });
                }
                // Duas
                if (this.request.selectedDuas.length > 0) {
                    // Si actualiza borro duas pertenecientes a la actividad
                    if (this.activity) UnitPlanActivityDua.delete(activityDua => activityDua.activity_id === this.activity.id);
                    this.request.selectedDuas.forEach(dua => {
                        UnitPlanActivityDua.insert({
                            data: {
                                activity_id: response.unitplan_activities[0].id,
                                dua_id: dua.id,
                            }
                        })
                    });
                }
                // Files
                this.request.files.forEach(file => {
                    UnitPlanActivityFile.insertOrUpdate({
                        data: {
                            activity_id: response.unitplan_activities[0].id,
                        }
                    })
                });

                this.$emit('activity-added');
                this.request = requestDefaultValues();
                this.show(false);
            })
        }
    },
});
