



























import Vue from "vue";
import highlighter from 'vue-highlight-words'
import {mapState} from "vuex";

export default Vue.extend({
    name: "RomaActivityList",
    props: {
        activity: {
            type: Object,
            required: true
        },
        color: {
            type: String,
            default: 'grey'
        },
    },
    data() {
        return {};
    },
    computed: {
      ...mapState({
          highlightWords (state) {
              return state.highlight.split(' ');
          }
      })
    },
    components: {
        highlighter
    }
});
