










































































import Vue from "vue";
import { createSimpleTransition } from 'vuetify/src/util/helpers';

import RomaActivityModalForm from "@/components/RomaActivityModalForm.vue";
import RomaActivity from "@/components/RomaActivity.vue";
import UnitPlanActivity from "@/models/UnitPlanActivity";

const romaActivityListTransition = createSimpleTransition('activity-list-transition');
Vue.component('my-transition', romaActivityListTransition);

export default Vue.extend({
    name: "RomaActivityList",
    props: {
      unit: {
          type: Object,
          required: true,
      },
    },
    data() {
        return {
            dialog: false,
            selectedActivity: null,
        };
    },
    computed: {
      activities () {
          const activities = UnitPlanActivity.query().
                              where('unitplan_id', this.unit.id).
                              with('duas').
                              with('files').
                              with('oas').
                              get();
          return activities;
      }
    },
    methods: {
        addActivity () {
            this.selectedActivity = null; // Reinicio actividad seleccionada si es nueva
            this.dialog = true;
        },
        editActivity (activity: Object) {
          this.selectedActivity = activity;
          this.dialog = true;
        },
        style (itemIndex: number) {
            const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
            let color = colors[itemIndex];
            color = `var(${colors[itemIndex % colors.length]})`;

            return color;
        }
    },
    components: {
        RomaActivityModalForm,
        RomaActivity
    }
});
