













































































































import Vue from "vue";
import { mapState } from 'vuex';

import RomaOa from "@/components/RomaOa.vue";
import Oa from "@/models/Oa";
import UnitPlanOa from "@/models/UnitPlanOa";

export default Vue.extend({
    name: "RomaOaModalForm",
    props: {
        value: Boolean,
        unit: {
            type: Object,
            required: true,
        },
        maxWidth: {
            type: String,
            default: '800',
        },
        overlayOpacity: {
            type: String,
            default: '0.8',
        },
        persistent: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {
            valid: false,
            loading: false,
            showing: this.value,
            oaView: RomaOa,
            subject: null,
            schoolLevel: null,
            level: null,
            courses: ['A', 'B', 'C', 'D', 'E', 'F'],
            request: {
                oas: []
            },
        };
    },
    computed: {
        ...mapState([
            'roma',
        ]),
        data () {
            let oas = Oa.query().with('ies').get();

            oas = oas.map(oa => {
                return new UnitPlanOa({
                    id: oa.id,
                    unitplan_id: this.unit.id,
                    oa_id: oa.id,
                    oa: oa,
                    selectedRows: false,
                })
            });

            return {details: oas};
        }
    },
    methods: {
        selectOAs (event) {
            this.request.oas = event;
        },
        setUnit () {
            UnitPlanOa.insert({
                data: this.request.oas.map(oa => {
                    return {
                        unitplan_id: oa.unitplan_id,
                        complement: true,
                        oa_id: oa.oa_id,
                    }
                })
            });
            this.show(false);
            this.request.oas = [];
            this.subject = null;
            this.schoolLevel = null;
            this.level = null;
        },
        show(value) {
            this.$emit('input', value);
        },
    },
});
