















































































































































































































import Vue from "vue";
import { Ripple } from 'vuetify/lib/directives/';
import { mask } from 'vue-the-mask';

// import RomaCoverage from "@/components/RomaCoverage.bak.vue";
import RomaOaModalForm from "@/components/RomaOaModalForm.vue";
import RomaConfirmDialog from '@/components/partial/RomaConfirmDialog.vue';
import RomaActivityModalForm from "@/components/RomaActivityModalForm.vue";
import UnitPlanOa from "@/models/UnitPlanOa";
import UnitPlanActivityOa from "@/models/UnitPlanActivityOa";

export default Vue.extend({
    name: "RomaOa",
    props: {
        unit: {
            type: Object,
            required: true,
        },
        height: {
            type: String,
            default: '514'
        },
        elevation: {
            type: Boolean,
            default: true,
        },
        hideHeader: {
            type: Boolean,
            default: false
        },
        hideFooter: {
            type: Boolean,
            default: false
        },
        hideBadges: {
            type: Boolean,
            default: false
        },
        hideEditHour: {
            type: Boolean,
            default: false
        },
        applyRowColor: {
            type: Boolean,
            default: true
        }
    },
    data() {
        return {
            dialog: false,
            selectedAll: false,
            dialogDeleteOA: false,
            dialogActivity: false,
            oaToDelete: null,
            mask: '##:##',
            selectedRows: [],
            expanded: [],
            buttons: [
                /*{
                    label: 'Cobertura',
                    icon: 'fas fa-history',
                    onClick: () => {
                        this.selectedRows.forEach(detail => {
                          if (!detail.disabled) {
                              detail.editing = !detail.editing;
                              // detail.selectedRows.ies.forEach(ie => {
                              //     ie.editing = !ie.editing;
                              // });
                          }
                        });
                    },
                },*/
                {
                    label: 'Actividades',
                    icon: 'fas fa-user-check',
                    onClick: () => {
                        this.dialogActivity = true;
                    },
                },
                {
                    label: 'Alternar activo/inactivo',
                    icon: 'fas fa-times-circle',
                    onClick: () => {
                        this.toggleOa(this.selectedRows);
                    },
                }
            ],
            oaDetails: [
                {
                    name: 'actividades',
                    icon: 'fas fa-user-check',
                    prop: 'activities',
                    color: 'secondary',
                    count: (oa) => {
                      return this.activitiesInOa(oa);
                    },
                    onClick: (oa) => {
                        this.$store.commit('highlightWords', oa.cod);
                        this.goActivityTab()
                    }
                },
                {
                    name: 'comentarios',
                    icon: 'fas fa-comment',
                    prop: 'comments',
                    color: 'primary',
                    count: (oa) => {
                        return 0;
                    },
                    onClick: () => {}
                },
            ],
            headerOAs: [
                {
                    text: '',
                    align: 'left',
                    value: 'oa.id',
                },
                { text: 'Nombre', value: 'oa.name' },
                { text: 'Comentarios', value: 'oa.comments' },
                { text: '', sortable: false },
                { text: '', value: 'data-table-expand', sortable: false },
            ],
            headerIEs: [
                { text: 'ID', value: 'id' },
                { text: 'Nombre', value: 'name' },
                {text: '', value: 'editIE'}
            ],
        };
    },
    computed: {
        isIndeterminate () {
            let opt = false;
            if (this.selectedRows.length !== this.unit.details.length && this.selectedRows.length !== 0) opt = true;
            return opt
        },
        totalSelectedIEs () {
          let sum = 0;
          if (this.selectedRows.length > 0) {
              sum = this.selectedRows.map(oa => oa.selectedRows.length);
              sum = sum.reduce((a, b) => a + b);
          }

          return sum;
        },
        totalHours () {
            let h= 0;
            let m = 0;

            const hours = this.oasAdded.map(item => item.hours);
            for (let i = 0; i < hours.length; i++) {
                let arr = hours[i].split(':');
                h += parseInt(arr[0]);
                m += parseInt(arr[1]);
                h += Math.floor(m / 60) ;
                m = m % 60;
            }

            if (isNaN(h) && isNaN(m)) {
                h = "00";
                m = "00";
            }

            return h + ' Horas - ' + m + ' Minutos';
        },
        oasAdded () {
            const oas = UnitPlanOa.query()
                              // TODO: CAMBIAR value por this.unit.id temporal para solucionar error de multi unidades
                              .where('unitplan_id', 1).
                              with('oa', query => {
                                  query.with('ies')
                              }).
                              get();
            console.log(oas)
            return oas;
        },
    },
    watch: {
      selectedRows: {
          deep: true,
          handler (value) {
              if (value.length === 0) this.selectedAll = false;
              this.$emit('oa-selected', this.selectedRows);
          }
      },
    },
    methods: {
      goActivityTab () {
          window.location.hash = 'tab-3';
      },
      selectOA ({item, value}) {
          item.selectedRows = value ? item.oa.ies : []; // Selecciona todos los ies de un oa
      },
      selectAllOAs () {
          if (this.selectedAll) {
              this.selectedRows = this.oasAdded;
              this.selectedRows.forEach(detail => {
                  detail.selectedRows = detail.oa.ies;
              });
          } else {
              this.selectedRows.forEach(detail => {
                  detail.selectedRows = [];
              });
              this.selectedRows = [];
          }
      },
      selectIE (oa: object, {value}) {
          const index = this.selectedRows.indexOf(oa);
          if (value) {
              if (index === -1) this.selectedRows.push(oa); // agrega a oa seleccionado si se marca un ie
          } else {
              if (oa.selectedRows.length === 1) {
                  this.selectedRows.splice(index, 1); // Si deshabilita el ultimo ie quito el oa de seleccionado
              }
          }
      },
      toggleOa (details) {
        if (Array.isArray(details)) {
            details.forEach((detail) => {
                detail.disabled = !detail.disabled;
                detail.hours = '00:00'; // Reinicio la hora para que no cuente en el total
                detail.selectedRows = [];
            });
            this.selectedRows = []; //unselect rows
        }
        UnitPlanOa.update({data: details});
      },
      goDelOA (oa) {
          this.dialogDeleteOA = true;
          this.oaToDelete = oa;
      },
      delOA () {
          UnitPlanOa.delete(this.oaToDelete.id);

          this.oaToDelete = null;
          this.dialogDeleteOA = false;
      },
      activitiesInOa (oa) {
          const count = UnitPlanActivityOa.
          query().
          with('oa', query => {
              query.findIn(this.unit.activities.map(a => a.id))
          }).
          where('oa_id', oa.id).
          count();
          return count;
      },
      rowStyle (itemIndex: number, disabled: boolean) {
          const colors = ['--v-roma_rcolor-base', '--v-primary-base', '--v-secondary-base', '--v-roma_acolor-base'];
          let color = colors[itemIndex];
          color = `var(${colors[itemIndex % colors.length]})`;

          if (disabled) color = 'grey';
          if (!this.applyRowColor) return false;

          return `border-left: 8px solid ${color};`;
      },
    },
    components: {
        // RomaCoverage,
        RomaOaModalForm,
        RomaConfirmDialog,
        RomaActivityModalForm,
    },
    directives: {
        mask,
        Ripple
    }
});
